<template>
	<v-container>
		<v-row align-content="center" justify="center">
			<v-col class="text-subtitle-1 text-center" cols="12">
				<v-alert
					:type="
						currentUser.type === 'user'
							? 'info'
							: winnwe
							? 'success'
							: 'error'
					"
					outlined
				>
					<h2 class="main-header" :style="titleStyle">{{ title }}</h2>
					<i style="font-size: small"
						>Race finished at {{ finishDate }}</i
					>
				</v-alert>
			</v-col>
		</v-row>
		<scores :sellers="raceDone.sellers" :myId="currentUser.id" />
		<v-row align-content="center" justify="center" v-if="winner">
			<v-col class="text-subtitle-1 text-center" cols="12">
				<article>
					Lorem, ipsum dolor sit amet consectetur adipisicing elit.
					Reprehenderit illo reiciendis sint dolorum cum architecto
					sapiente saepe at. Repellat in amet exercitationem
					temporibus illum suscipit similique maiores fugit atque
					dolores.
				</article>
			</v-col>
		</v-row>
		<v-row
			align-content="center"
			justify="center"
			v-if="winner && !currentRace.invoice"
		>
			<v-btn>Generate invoice</v-btn>
		</v-row>
		<v-row
			align-content="center"
			justify="center"
			v-if="winner && currentRace.invoice"
		>
			<v-btn @click="resetSeller"
				>Forguet race and start from scratch</v-btn
			>
		</v-row>
	</v-container>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Scores from "../components/Scores.vue";
export default {
	components: { Scores },
	computed: {
		...mapState("common", ["currentUser"]),
		raceDone() {
			return this.currentUser.raceDone;
		},
		winner() {
			this.raceDone.winner === this.currentUser.id;
		},
		title() {
			return this.winner
				? "Congrats, you Won"
				: `Race has finished, ${this.raceDone.winner} won`;
		},
		finishDate() {
			return this.raceDone.timestamp;
		},
		titleStyle() {
			return this.currentUser.type === "user"
				? "color:blue"
				: this.winner
				? "color:green"
				: "color:red";
		},
	},
	methods: {
		...mapActions("common", ["clearCurrentUser"]),
		resetSeller() {
			if (this.currentUser.type !== seller) return;
			let user = this.currentUser;
			user.raceUser = undefined;
			user.raceDone = undefined;
			this.clearCurrentUser();
		},
	},
};
</script>

